import {getEmployeeById, addDepartureEmployee} from '../../server/employee'
import {MessageBox, Indicator} from 'mint-ui';

export default {
  name: 'addEmployee',
  data() {
    //     var loginName = (rule, value, callback) => {
    //     if (!value) {
    //       return callback(new Error('请输入手机号 / 用户名'));
    //     }else{
    //       callback();
    //     }
    //     }
    // var passWord = (rule, value, callback) => {
    //     if (!value) {
    //       return callback(new Error('请输入密码'));
    //     }else{
    //       callback();
    //     }
    //     }
    return {
      employeeName: '',//姓名
      value: '',
      employeeArea: '',//所属区域
      customerEmployeeNo: '',//工号
      organizationalId: '',//所属业务区
      entryAt: '',//离职日期
      entryAt1: '',//社保日期
      entryAt2: '',//结束社保日期
      customerId: '',
      positionType: '',
      nowDate: '',
      clear:false,
      docmHeight: document.documentElement.clientHeight,  //默认屏幕高度
      showHeight: document.documentElement.clientHeight,   //实时屏幕高度
      hideshow: true,  //显示或者隐藏footer
    }
  },
  created() {
    this.getUserData();

  },
  mounted() {
    const _this = this;
    let clear =  document.createElement("div");
    clear.innerText='清除日期';
    clear.className='clear';
    clear.style.cssText="margin-left:20px";
    $(".mint-datetime-cancel")[0].style.display="flex";
    // window.onresize监听页面高度的变化
    $(".mint-datetime-cancel")[0].append(clear);
    clear.addEventListener("click",()=>{
      switch (_this.nowDate) {
        case 1:
          _this.entryAt1 = " ";
          break;
        case 2:
          _this.entryAt2 = " ";
          break;
      }
      $(".mint-datetime-cancel")[0].click();
    },false);
    window.onresize = () => {
      return (() => {
        this.showHeight = document.body.clientHeight;
      })()
    };
  },
  //监听
  watch: {
    showHeight: function () {
      if (this.docmHeight > this.showHeight) {
        this.hideshow = false
      } else {
        this.hideshow = true
      }
    }
  },
  methods: {
    clearInput(input) {
      this[input] = " ";
    },
    openPicker(value) {
      this.value = new Date();
      this.nowDate = value;
      this.$refs.picker.open();
      switch (this.nowDate) {
        case 0:
          this.$refs.picker.$el.getElementsByClassName('picker-slot')[2].style.display = 'block';
          $(".clear")[0].style.display="none";
          break;
        case 1:
          this.$refs.picker.$el.getElementsByClassName('picker-slot')[2].style.display = 'none';
          $(".clear")[0].style.display="block";
          break;
        case 2:
          this.$refs.picker.$el.getElementsByClassName('picker-slot')[2].style.display = 'none';
          $(".clear")[0].style.display="block";
          break;
      }
    },
    handleConfirm(value) {
      this.clear = false;
      switch (this.nowDate) {
        case 0:
          this.entryAt = this.$utils.date(value, '4');
          break;
        case 1:
          this.entryAt1 = this.$utils.date(value, '5');
          break;
        case 2:
          this.entryAt2 = this.$utils.date(value, '5');
          break;
      }
    },
    handleService() {
      this.$router.push({
        path: '/SelectService'
      })
    },
    getUserData: function () {
      const userid = this.$route.params.id, _this = this;
      Indicator.open();
      getEmployeeById(userid).then(e => {
        Indicator.close();
        let {employeeName, organizationName, customerEmployeeNo, entryAt, socialSecurityStartAt} = e.data.data;
        entryAt = _this.$utils.date(entryAt, '4');
        socialSecurityStartAt = socialSecurityStartAt == null ? '' : _this.$utils.date(socialSecurityStartAt, '5');
        if (e.data.code == 200) {
          _this.employeeName = employeeName;
          _this.employeeArea = organizationName;
          _this.customerEmployeeNo = customerEmployeeNo;
          _this.organizationalId = entryAt;
          _this.entryAt1 = socialSecurityStartAt || ' ';
        }
      });
    },
    handleClient() {
      this.$router.push({
        path: '/ClientType'
      })
    },
    openlabour() {
      this.$refs.picker.open();
    },
    handleType() {
      console.log(1)
    },
    handleSave: function (type) {
      const userid = this.$route.params.id, _this = this;
      let {organizationalId, entryAt, entryAt1, entryAt2} = this;
      organizationalId = new Date(organizationalId);
      entryAt = new Date(entryAt);
      entryAt1 = new Date(entryAt1);
      entryAt2 = new Date(entryAt2);
      if (userid === 'id') {
        MessageBox({
          title: '提示',
          message: '请先选择离职员工',
        });
        return;
      }
      if (this.positionType === '' || this.entryAt === '') {
        MessageBox({
          title: '提示',
          message: '请填写完整信息',
        });
        return;
      }
      if (entryAt < organizationalId) {
        MessageBox({
          title: '提示',
          message: '离职日期不能小于入职日期!',
        });
        return;
      }
      if (entryAt2 < entryAt1) {
        MessageBox({
          title: '提示',
          message: '结束缴纳日期不能小于开始缴纳日期!',
        });
        return;
      }
      addDepartureEmployee({
        "employee_id": userid,
        "resignationAt": this.entryAt,
        "resignationReason": this.positionType,//所属业务id
        "socialSecurityStartAt": (this.entryAt1 === ' ') ? '' : this.entryAt1,
        "socialSecurityEndAt": (this.entryAt2 === ' ') ? '' : this.entryAt2,
      }).then(e => {
        if (e.data.code === 200) {
          type ? (_this.$router.replace('/Employee'), MessageBox({
            title: '提示',
            message: "离职信息登记完成",
          })) : (_this.$router.replace('/leaveEmployee/id'), _this.$router.go(0));
        } else {
          MessageBox({
            title: '提示',
            message: e.data.message,
          });
        }
      })
    }
  }
}



